import React, { Component } from "react";

import {
  API,
  ENDPOINT,
  ACCESS_KEY,
  BASE_CURRENCY,
  SYMBOLS
} from "../../helpers/GoldApiKW";
import "../../App.css";
import "../../styles.css";

class GoldZakatKW extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rates: [],
      timestamp: "",
      isLoading: false,
      inTwentyFourKarat: "",
      opTwentyFourKarat: "",
      inTwentyTwoKarat: "",
      opTwentyTwoKarat: "",
      inTwentyOneKarat: "",
      opTwentyOneKarat: "",
      inEighteenKarat: "",
      opEighteenKarat: ""
    };
    this.handleInputChange = this.handleInputChange.bind(this);
  }

  componentDidMount() {
    this.intervalId = setInterval(() => this.getData(), 3600000);
    this.getData();
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  getData = () => {
    this.setState({ isLoading: true });
    fetch(
      API + ENDPOINT + "?access_key=" + ACCESS_KEY + BASE_CURRENCY + SYMBOLS
    )
      .then((response) => response.json())
      .then((data) =>
        this.setState({
          rates: data.rates,
          timestamp: data.timestamp,
          isLoading: false
        })
      );
  };

  handleInputChange = (event) => {
    if (event.target.name === "inTwentyFourKarat") {
      this.setState({
        inTwentyFourKarat: event.target.value,
        opTwentyFourKarat: Number((event.target.value * 24) / 24).toFixed(3)
      });
    } else if (event.target.name === "inTwentyTwoKarat") {
      this.setState({
        inTwentyTwoKarat: event.target.value,
        opTwentyTwoKarat: Number((event.target.value * 22) / 24).toFixed(3)
      });
    } else if (event.target.name === "inTwentyOneKarat") {
      this.setState({
        inTwentyOneKarat: event.target.value,
        opTwentyOneKarat: Number((event.target.value * 21) / 24).toFixed(3)
      });
    } else if (event.target.name === "inEighteenKarat") {
      this.setState({
        inEighteenKarat: event.target.value,
        opEighteenKarat: Number((event.target.value * 18) / 24).toFixed(3)
      });
    } else {
      this.setState({
        inEighteenKarat: ""
      });
    }
  };

  render() {
    const {
      isLoading,
      rates,
      inTwentyFourKarat,
      opTwentyFourKarat,
      inTwentyTwoKarat,
      opTwentyTwoKarat,
      inTwentyOneKarat,
      opTwentyOneKarat,
      inEighteenKarat,
      opEighteenKarat
    } = this.state;
    if (isLoading) {
      return (
        <div>
          <h1>LOADING ....</h1>
        </div>
      );
    }
    return (
      <div className="App" dir="rtl">
        <div>حاسبة زكاة الذهب</div>

        <div className="row-zakat">
          <label>
            <small className="font-weight-bold">أدخل وزن الذهب عيار 24</small>
            <input
              name="inTwentyFourKarat"
              style={{ width: "120px", borderColor: "#d4af37" }}
              placeholder="0.000"
              type="number"
              value={inTwentyFourKarat}
              onChange={this.handleInputChange}
            />
          </label>
          <label>
            <small className="font-weight-bold">مقدار الذهب عيار 24</small>
            <input
              placeholder="0.000"
              type="number"
              name="opTwentyFourKarat"
              disabled
              readOnly
              value={Number((inTwentyFourKarat * 24) / 24).toFixed(3)}
            />
          </label>
        </div>
        <div className="row-zakat">
          <label>
            <small className="font-weight-bold">أدخل وزن الذهب عيار 22</small>
            <input
              name="inTwentyTwoKarat"
              style={{ width: "120px", borderColor: "#d4af37" }}
              placeholder="0.000"
              type="number"
              value={inTwentyTwoKarat}
              onChange={this.handleInputChange}
            />
          </label>
          <label>
            <small className="font-weight-bold">مقدار الذهب 22/24</small>
            <input
              placeholder="0.000"
              type="number"
              name="opTwentyTwoKarat"
              readOnly
              disabled
              value={Number((inTwentyTwoKarat * 22) / 24).toFixed(3)}
            />
          </label>
        </div>
        <div className="row-zakat">
          <label>
            <small className="font-weight-bold">أدخل وزن الذهب عيار 21</small>
            <input
              name="inTwentyOneKarat"
              style={{ width: "120px", borderColor: "#d4af37" }}
              placeholder="0.000"
              type="number"
              value={inTwentyOneKarat}
              onChange={this.handleInputChange}
            />
          </label>
          <label>
            <small className="font-weight-bold">مقدار الذهب 21/24</small>
            <input
              placeholder="0.000"
              type="number"
              name="opTwentyOneKarat"
              readOnly
              disabled
              value={Number((inTwentyOneKarat * 21) / 24).toFixed(3)}
            />
          </label>
        </div>
        <div className="row-zakat">
          <label>
            <small className="font-weight-bold">أدخل وزن الذهب عيار 18</small>
            <input
              name="inEighteenKarat"
              style={{ width: "120px", borderColor: "#d4af37" }}
              placeholder="0.000"
              type="number"
              value={inEighteenKarat}
              onChange={this.handleInputChange}
            />
          </label>

          <label>
            <small className="font-weight-bold">مقدار الذهب 18/24</small>
            <input
              placeholder="0.000"
              type="number"
              name="opEighteenKarat"
              readOnly
              disabled
              value={Number((inEighteenKarat * 18) / 24).toFixed(3)}
            />
          </label>
        </div>

        <div className="column-zakat">
          <label>
            <small className="font-weight-bold">
              مجموع مقدار النصاب بالجرام
            </small>
            <input
              placeholder="0.000"
              readOnly
              disabled
              value={Number(
                +opTwentyFourKarat +
                  +opTwentyTwoKarat +
                  +opTwentyOneKarat +
                  +opEighteenKarat
              ).toFixed(3)}
            />
          </label>
          {Number(
            +opTwentyFourKarat +
              +opTwentyTwoKarat +
              +opTwentyOneKarat +
              +opEighteenKarat
          ).toFixed(3) >= 85 ? (
            <div>
              لقد تحقق مقدار الزكاة
              <div className="row-zakat">
                <label>
                  <small className="font-weight-bold">
                    سعر جرام الذهب بالدينار
                  </small>
                  <input
                    placeholder="0.000"
                    readOnly
                    disabled
                    value={Number(rates.XAU / 31.1034807).toFixed(3)}
                  />
                </label>
                <label>
                  <small className="font-weight-bold">
                    الزكاة المقدرة تقريبا
                  </small>
                  <input
                    placeholder="0.000"
                    readOnly
                    disabled
                    value={Number(
                      (rates.XAU / 31.1034807) *
                        (+opTwentyFourKarat +
                          +opTwentyTwoKarat +
                          +opTwentyOneKarat +
                          +opEighteenKarat) *
                        0.025
                    ).toFixed(3)}
                  />
                </label>
              </div>
            </div>
          ) : (
            <div className="row">لم يتحقق مقدار الزكاة لأنك لم تصل للنصاب</div>
          )}
        </div>
        <div>مقدا الزكاة تقريبية و ليست نهائية و كذلك مقدار الزكاة</div>
      </div>
    );
  }
}

export default GoldZakatKW;
