import React, { Component } from "react";
import ReactLoading from "react-loading";

import {
  API,
  ACCESS_KEY,
  BASE_CURRENCY,
  SYMBOLS,
  PreviousDay,
  CurrentDay,
  FLUCTUATION
} from "../../helpers/GoldApiKW";

class GoldTicker extends Component {
  constructor(props) {
    super(props);
    this.state = {
      timestamp: "",
      isLoading: false,
      PreviousDay: PreviousDay,
      CurrentDay: CurrentDay,
      FluctuationRates: []
    };
  }

  componentDidMount() {
    this.intervalId = setInterval(() => this.getData(), 60000);
    this.getData();
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  getData = () => {
    this.setState({ isLoading: true });
    fetch(
      API +
        FLUCTUATION +
        "?access_key=" +
        ACCESS_KEY +
        "&start_date=" +
        PreviousDay +
        "&end_date=" +
        CurrentDay +
        SYMBOLS +
        BASE_CURRENCY
    )
      .then((response) => response.json())
      .then((data) =>
        this.setState({
          FluctuationRates: data.rates.XAU,
          isLoading: false
        })
      );
  };

  render() {
    const { FluctuationRates, isLoading } = this.state;
    console.log(FluctuationRates.change);
    if (isLoading) {
      return (
        <div className="sj_logo">
          <h1>Loading Gold Prices...</h1>

          <ReactLoading
            className="sj_logo"
            type="bars"
            color="#c4ae78"
            height={200}
            width={200}
          />
        </div>
      );
    }

    return (
      <div>
        {" "}
        {Number(
          (FluctuationRates.start_rate / 31.1034807).toFixed(3) >
            (FluctuationRates.end_rate / 31.1034807).toFixed(3)
        ) ? (
          <div className="price-down">
            {" "}
            <p>Down &#x25BC;</p> <p>نازل عن أمس</p>{" "}
            <p>
              Yesterday Closing Price ({" "}
              {Number(FluctuationRates.start_rate / 31.1034807).toFixed(4)} )
              K.D/gr
            </p>{" "}
            <p>
              Daily Change ({"- "}
              {Number(FluctuationRates.change / 31.1034807).toFixed(4)} )
              Fills/gr
            </p>{" "}
            <p>
              Daily Change Perctentage (
              {Number(FluctuationRates.change_pct).toFixed(4)}
              {" %"} ) Fills/gr
            </p>{" "}
          </div>
        ) : (
          <div className="price-up">
            {" "}
            <p>UP &#x25B2;</p> <p>مرتفع عن أمس</p>{" "}
            <span>
              {" "}
              <p>
                Yesterday Closing Price ({"+ "}
                {Number(FluctuationRates.start_rate / 31.1034807).toFixed(4)} )
                Fills/gr
              </p>{" "}
              <p>
                Daily Change ({"+ "}
                {Number(FluctuationRates.change / 31.1034807).toFixed(4)} )
                Fills/gr
              </p>{" "}
              <p>
                Daily Change Perctentage ({"+ "}
                {Number(FluctuationRates.change_pct).toFixed(4)}
                {" %"} ){" "}
              </p>{" "}
            </span>{" "}
            <div></div>{" "}
          </div>
        )}{" "}
      </div>
    );
  }
}
export default GoldTicker;
