import React, { Component } from "react";
import ReactLoading from "react-loading";
import { Container, Image } from "react-bootstrap";

import {
  API,
  ENDPOINT,
  ACCESS_KEY,
  BASE_CURRENCY,
  SYMBOLS
} from "../../helpers/GoldApiKW";

class AloraPriceKW extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rates: [],
      timestamp: "",
      isLoading: false
    };
  }

  componentDidMount() {
    this.intervalId = setInterval(() => this.getData(), 60000);
    this.getData();
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  getData = () => {
    this.setState({ isLoading: true });
    fetch(
      API + ENDPOINT + "?access_key=" + ACCESS_KEY + BASE_CURRENCY + SYMBOLS
    )
      .then((response) => response.json())
      .then((data) =>
        this.setState({
          rates: data.rates,
          timestamp: data.timestamp,
          isLoading: false
        })
      );
  };

  render() {
    const { rates, isLoading } = this.state;

    if (isLoading) {
      return (
        <div className="sj_logo">
          <h1>Loading Gold Prices...</h1>

          <ReactLoading
            className="sj_logo"
            type="bars"
            color="#c4ae78"
            height={200}
            width={200}
          />
        </div>
      );
    }

    return (
      <div className="price-table">
        <Container fluid className="mt-4">
          <h3>Today's ALORA Gold Bars (999.9) Prices in KWD</h3>
          <h3>اسعار سبائك الورا الكويتية بالدينار الكويتي حاليا</h3>
        </Container>
        <table className="price-table">
          <thead>
            <tr>
              <th colSpan="2">
                <Container className="price-container">
                  <Image src="assets/imgs/alora.jpg" className="bar-brand" />
                  <h3 className="price-title">ALORA FLOWER (KUWAIT 999.9)</h3>
                </Container>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td dir="rtl">
                {Number(rates.XAU / 31.1034807 + 5.22).toFixed(3)} د.ك
              </td>
              <td>1 gr</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number((rates.XAU / 31.1034807) * 5 + 8.3).toFixed(3)} د.ك
              </td>
              <td>5 gr</td>
            </tr>
            <tr>
              <td dir="rtl">
                {" "}
                {Number((rates.XAU / 31.1034807) * 8 + 12.5).toFixed(3)} د.ك
              </td>
              <td>8 gr (Coin Lira Size)</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number((rates.XAU / 31.1034807) * 10 + 12.3).toFixed(3)} د.ك
              </td>

              <td>10 gr</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number((rates.XAU / 31.1034807) * 20 + 15.3).toFixed(3)} د.ك
              </td>
              <td>20 gr</td>
            </tr>
            <tr>
              <td dir="rtl">
                {" "}
                {Number((rates.XAU / 31.1034807) * 31.1 + 18.3).toFixed(3)} د.ك
              </td>
              <td>1 Oz</td>
            </tr>
            <tr>
              <td dir="rtl">
                {" "}
                {Number((rates.XAU / 31.1034807) * 50 + 22.3).toFixed(3)} د.ك
              </td>
              <td>50 gr</td>
            </tr>
            <tr>
              <td dir="rtl">
                {" "}
                {Number((rates.XAU / 31.1034807) * 100 + 32.3).toFixed(3)} د.ك
              </td>
              <td>100 gr</td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export default AloraPriceKW;
