import React, { Component } from "react";
import ReactLoading from "react-loading";
import { Container, Image } from "react-bootstrap";
import "../../App.css";
import "../../styles.css";

import {
  API,
  ACCESS_KEY,
  ENDPOINT,
  BASE_CURRENCY,
  SYMBOLS
} from "../../helpers/GoldApiKW";

class GoldCalc875 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      timestamp: "",
      isLoading: false,
      rates: [],
      InputGram: "",
      InputPrice: "",
      is9999: false,
      is9170: false,
      is8750: false,
      is7500: false
    };
    this.updateInputgrams = this.updateInputgrams.bind(this);
    this.updateInputPrice = this.updateInputPrice.bind(this);
    this.handleChangeTo9999 = this.handleChangeTo9999.bind(this);
    this.handleChangeTo9170 = this.handleChangeTo9170.bind(this);
    this.handleChangeTo8750 = this.handleChangeTo8750.bind(this);
    this.handleChangeTo7500 = this.handleChangeTo7500.bind(this);
  }

  componentDidMount() {
    this.intervalApi = setInterval(() => this.getData(), 120000);
    this.getData();
  }

  componentWillUnmount() {
    clearInterval(this.intervalApi);
  }
  handleChangeTo9999 = () => {
    this.setState({
      is9999: true,
      is9170: false,
      is8750: false,
      is7500: false
    });
  };
  handleChangeTo9170 = () => {
    this.setState({
      is9999: false,
      is9170: true,
      is8750: false,
      is7500: false
    });
  };
  handleChangeTo8750 = () => {
    this.setState({
      is9999: false,
      is9170: false,
      is8750: true,
      is7500: false
    });
  };
  handleChangeTo7500 = () => {
    this.setState({
      is9999: false,
      is9170: false,
      is8750: false,
      is7500: true
    });
  };
  getData = () => {
    this.setState({ isLoading: true });
    fetch(
      API + ENDPOINT + "?access_key=" + ACCESS_KEY + BASE_CURRENCY + SYMBOLS
    )
      .then((response) => response.json())
      .then((data) =>
        this.setState({
          rates: data.rates,
          timestamp: data.timestamp,
          isLoading: false
        })
      );
  };
  updateInputgrams(event) {
    this.setState({ InputGram: event.target.value });
  }
  updateInputPrice(event) {
    this.setState({ InputPrice: event.target.value });
  }
  render() {
    const {
      isLoading,

      rates,
      is9999,
      is9170,
      is8750,
      is7500
    } = this.state;

    if (isLoading) {
      return (
        <div className="App sj_logo">
          <h1>Loading Gold Prices...</h1>

          <ReactLoading
            className="sj_logo"
            type="bars"
            color="#c4ae78"
            height={200}
            width={200}
          />
        </div>
      );
    }

    return (
      <div className="App">
        <div>
          <Container fluid className="mt-4">
            <h3>Today's Gold Price KWD/Gr.</h3>
            <h3>سيتم تحديث الاسعار كل دقيقة</h3>
            <h3>إختر عيار الذهب</h3>
          </Container>
          <div className="row">
            <button
              className="btn btn-dark btn-sm"
              onClick={this.handleChangeTo9999}
            >
              <Container className="karat-btn">
                <Image src="assets/btns/999-btn.jpg" className="karat-btn" />
              </Container>
            </button>
            <button
              className="btn btn-dark btn-sm"
              onClick={this.handleChangeTo9170}
            >
              <Container className="karat-btn">
                <Image src="assets/btns/917-btn.jpg" className="karat-btn" />
              </Container>
            </button>
            <button
              className="btn btn-dark btn-sm"
              onClick={this.handleChangeTo8750}
            >
              <Container className="karat-btn">
                <Image src="assets/btns/875-btn.jpg" className="karat-btn" />
              </Container>
            </button>
            <button
              className="btn btn-dark btn-sm"
              onClick={this.handleChangeTo7500}
            >
              <Container className="karat-btn">
                <Image src="assets/btns/750-btn.jpg" className="karat-btn" />
              </Container>
            </button>
          </div>
          <div className="row"></div>
          {is9999 === true ? (
            <div>
              {" "}
              <table className="price-table">
                <tbody>
                  <tr>
                    <td dir="rtl">
                      {Number(rates.XAU / 31.1034807 - 0.225).toFixed(3)} د.ك /
                      جرام
                    </td>
                    <td>عيار 24</td>
                    <td dir="rtl"> سعر البيع اليوم</td>
                  </tr>
                </tbody>
              </table>
              <table className="clac-table">
                <tbody>
                  <tr>
                    <td>
                      <label dir="rtl">
                        أدخل سعر البيع د.ك :
                        <input
                          type="text"
                          onChange={this.updateInputPrice}
                        ></input>
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label dir="rtl">
                        أدخل الوزن بالجرام :
                        <input
                          type="text"
                          onChange={this.updateInputgrams}
                        ></input>
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label dir="rtl">
                        قيمة الذهب د.ك :
                        <input
                          type="number"
                          readOnly
                          value={Number(
                            this.state.InputGram *
                              (rates.XAU / 31.1034807 - 0.225)
                          ).toFixed(3)}
                        />
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td dir="rtl">
                      إجمالي العمولة د.ك :
                      <input
                        type="number"
                        readOnly
                        value={Number(
                          this.state.InputPrice -
                            (
                              this.state.InputGram *
                              (rates.XAU / 31.1034807 - 0.225)
                            ).toFixed(3)
                        ).toFixed(3)}
                      />
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          ) : null}
          {is9170 === true ? (
            <div>
              {" "}
              <table className="price-table">
                <tbody>
                  <tr>
                    <td dir="rtl">
                      {Number((rates.XAU / 31.1034807) * 0.917 - 0.225).toFixed(
                        3
                      )}{" "}
                      د.ك / جرام
                    </td>
                    <td>عيار 24</td>
                    <td dir="rtl"> سعر البيع اليوم</td>
                  </tr>
                </tbody>
              </table>
              <table className="clac-table">
                <tbody>
                  <tr>
                    <td>
                      <label dir="rtl">
                        أدخل سعر البيع د.ك :
                        <input
                          type="text"
                          onChange={this.updateInputPrice}
                        ></input>
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label dir="rtl">
                        أدخل الوزن بالجرام :
                        <input
                          type="text"
                          onChange={this.updateInputgrams}
                        ></input>
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label dir="rtl">
                        قيمة الذهب د.ك :
                        <input
                          type="number"
                          readOnly
                          value={Number(
                            this.state.InputGram *
                              ((rates.XAU / 31.1034807) * 0.917 - 0.225)
                          ).toFixed(3)}
                        />
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td dir="rtl">
                      المصنعية د.ك على كل جرام :
                      <input
                        type="number"
                        readOnly
                        value={Number(
                          this.state.InputPrice / this.state.InputGram -
                            ((rates.XAU / 31.1034807) * 0.917 - 0.225)
                        ).toFixed(3)}
                      />
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          ) : null}
          {is8750 === true ? (
            <div>
              {" "}
              <table className="price-table">
                <tbody>
                  <tr>
                    <td dir="rtl">
                      {Number((rates.XAU / 31.1034807) * 0.875 - 0.225).toFixed(
                        3
                      )}{" "}
                      د.ك / جرام
                    </td>
                    <td>عيار 24</td>
                    <td dir="rtl"> سعر البيع اليوم</td>
                  </tr>
                </tbody>
              </table>
              <table className="clac-table">
                <tbody>
                  <tr>
                    <td>
                      <label dir="rtl">
                        أدخل سعر البيع د.ك :
                        <input
                          type="text"
                          onChange={this.updateInputPrice}
                        ></input>
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label dir="rtl">
                        أدخل الوزن بالجرام :
                        <input
                          type="text"
                          onChange={this.updateInputgrams}
                        ></input>
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label dir="rtl">
                        قيمة الذهب د.ك :
                        <input
                          type="number"
                          readOnly
                          value={Number(
                            this.state.InputGram *
                              ((rates.XAU / 31.1034807) * 0.875 - 0.225)
                          ).toFixed(3)}
                        />
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td dir="rtl">
                      المصنعية د.ك على كل جرام :
                      <input
                        type="number"
                        readOnly
                        value={Number(
                          this.state.InputPrice / this.state.InputGram -
                            ((rates.XAU / 31.1034807) * 0.875 - 0.225)
                        ).toFixed(3)}
                      />
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          ) : null}
          {is7500 === true ? (
            <div>
              {" "}
              <table className="price-table">
                <tbody>
                  <tr>
                    <td dir="rtl">
                      {Number((rates.XAU / 31.1034807) * 0.75 - 0.225).toFixed(
                        3
                      )}{" "}
                      د.ك / جرام
                    </td>
                    <td>عيار 24</td>
                    <td dir="rtl"> سعر البيع اليوم</td>
                  </tr>
                </tbody>
              </table>
              <table className="clac-table">
                <tbody>
                  <tr>
                    <td>
                      <label dir="rtl">
                        أدخل سعر البيع د.ك :
                        <input
                          type="text"
                          onChange={this.updateInputPrice}
                        ></input>
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label dir="rtl">
                        أدخل الوزن بالجرام :
                        <input
                          type="text"
                          onChange={this.updateInputgrams}
                        ></input>
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <label dir="rtl">
                        قيمة الذهب د.ك :
                        <input
                          type="number"
                          readOnly
                          value={Number(
                            this.state.InputGram *
                              ((rates.XAU / 31.1034807) * 0.75 - 0.225)
                          ).toFixed(3)}
                        />
                      </label>
                    </td>
                  </tr>
                  <tr>
                    <td dir="rtl">
                      المصنعية د.ك على كل جرام :
                      <input
                        type="number"
                        readOnly
                        value={Number(
                          this.state.InputPrice / this.state.InputGram -
                            ((rates.XAU / 31.1034807) * 0.75 - 0.225)
                        ).toFixed(3)}
                      />
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          ) : null}
        </div>
      </div>
    );
  }
}
export default GoldCalc875;
