import React, { Component } from "react";
import ReactLoading from "react-loading";
import { Container } from "react-bootstrap";

import {
  API,
  ENDPOINT,
  ACCESS_KEY,
  BASE_CURRENCY,
  SYMBOLS
} from "../../helpers/GoldApiKW";

class GoldPriceKW extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rates: [],
      timestamp: "",
      isLoading: false
    };
  }

  componentDidMount() {
    this.intervalId = setInterval(() => this.getData(), 60000);
    this.getData();
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  getData = () => {
    this.setState({ isLoading: true });
    fetch(
      API + ENDPOINT + "?access_key=" + ACCESS_KEY + BASE_CURRENCY + SYMBOLS
    )
      .then((response) => response.json())
      .then((data) =>
        this.setState({
          rates: data.rates,
          timestamp: data.timestamp,
          isLoading: false
        })
      );
  };

  render() {
    const { rates, isLoading } = this.state;

    if (isLoading) {
      return (
        <div className="sj_logo">
          <h1>Loading Gold Prices...</h1>

          <ReactLoading
            className="sj_logo"
            type="bars"
            color="#c4ae78"
            height={200}
            width={200}
          />
        </div>
      );
    }

    return (
      <div className="price-table">
        <Container fluid className="mt-4">
          <h3>Today's Gold Price KWD/Gr.</h3>
          <h3>سيتم تحديث الاسعار كل دقيقة</h3>
        </Container>
        <div className="date">
          {new Date().toLocaleString("en-US", { day: "2-digit" })}/
          {new Date().toLocaleString("en-US", { month: "2-digit" })}/
          {new Date().getFullYear()}
          <p>
            {new Date().toLocaleString("en-US", {
              hour: "numeric",
              minute: "numeric",
              second: "numeric",
              hour12: true
            })}
          </p>
        </div>
        <table className="price-table">
          <tbody>
            <tr>
              <td dir="rtl">
                {Number(rates.XAU / 31.1034807 + 0.0175).toFixed(3)} د.ك / جرام
              </td>
              <td>عيار 24</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number((rates.XAU / 31.1034807) * 0.916667 + 0.0175).toFixed(
                  3
                )}{" "}
                د.ك / جرام
              </td>
              <td>عيار 22</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number((rates.XAU / 31.1034807) * 0.875 + 0.0155).toFixed(3)}{" "}
                د.ك / جرام
              </td>

              <td>عيار 21</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number((rates.XAU / 31.1034807) * 0.75 + 0.0135).toFixed(3)}{" "}
                د.ك / جرام
              </td>
              <td>عيار 18</td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export default GoldPriceKW;
