import "./styles.css";
import React, { Component } from "react";
import GoldPriceKW from "./components/goldPrice/GoldPriceKW";
import GoldTicker from "./components/goldTracker/GoldTicker";
import AloraPriceKW from "./components/aloraPrice/AloraPrices";
import LiraPriceKW from "./components/goldPrice/LiraPriceKW";
import PampFortunaPrice from "./components/pampPrice/PampFortunaPrice";
import PampRosaPrice from "./components/pampPrice/PampRosaPrice";
import PampSpecialEditionPrice from "./components/pampPrice/PampSpecialEditionPrice";
import GoldCalc875 from "./components/goldCalc/GoldCalc875";
import GoldZakatKW from "./components/advCalc/goldZakat";
import { Container, Image } from "react-bootstrap";
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isGoldPriceKW: false,
      isAloraPriceKW: false,
      isLiraPriceKW: false,
      isPampFPriceKW: false,
      isPampRPriceKW: false,
      isPampSPricKW: false,
      isGoldCalc: false,
      isgoldNews: false
    };
    this.handleChangeToGoldPriceKw = this.handleChangeToGoldPriceKw.bind(this);
    this.handleChangeToAloraPriceKW = this.handleChangeToAloraPriceKW.bind(
      this
    );
    this.handleChangeToLiraPriceKW = this.handleChangeToLiraPriceKW.bind(this);
    this.handleChangeToPampFKW = this.handleChangeToPampFKW.bind(this);
    this.handleChangeToGoldCalc = this.handleChangeToGoldCalc.bind(this);
    this.handleChangeToGoldZakat = this.handleChangeToGoldZakat.bind(this);
  }

  handleChangeToGoldPriceKw = () => {
    this.setState({
      isGoldPriceKW: true,
      isAloraPriceKW: false,
      isLiraPriceKW: false,
      isPampFPriceKW: false,
      isPampRPriceKW: false,
      isGoldCalc: false,
      isPampSPricKW: false,
      isgoldNews: false
    });
  };

  handleChangeToAloraPriceKW = () => {
    this.setState({
      isGoldPriceKW: false,
      isAloraPriceKW: true,
      isLiraPriceKW: false,
      isPampFPriceKW: false,
      isPampRPriceKW: false,
      isGoldCalc: false,
      isPampSPricKW: false,
      isgoldNews: false
    });
  };
  handleChangeToLiraPriceKW = () => {
    this.setState({
      isGoldPriceKW: false,
      isAloraPriceKW: false,
      isLiraPriceKW: true,
      isPampFPriceKW: false,
      isPampRPriceKW: false,
      isPampSPricKW: false,
      isGoldCalc: false,
      isgoldNews: false
    });
  };
  handleChangeToPampFKW = () => {
    this.setState({
      isGoldPriceKW: false,
      isAloraPriceKW: false,
      isLiraPriceKW: false,
      isGoldCalc: false,
      isPampFPriceKW: true,
      isPampRPriceKW: true,
      isPampSPricKW: true,
      isgoldNews: false
    });
  };
  handleChangeToGoldCalc = () => {
    this.setState({
      isGoldPriceKW: false,
      isAloraPriceKW: false,
      isLiraPriceKW: false,
      isPampFPriceKW: false,
      isPampRPriceKW: false,
      isPampSPricKW: false,
      isGoldCalc: true,
      isgoldNews: false
    });
  };
  handleChangeToGoldZakat = () => {
    this.setState({
      isGoldPriceKW: false,
      isAloraPriceKW: false,
      isLiraPriceKW: false,
      isPampFPriceKW: false,
      isPampRPriceKW: false,
      isPampSPricKW: false,
      isGoldCalc: false,
      isgoldNews: true
    });
  };
  render() {
    const {
      isGoldPriceKW,
      isAloraPriceKW,
      isLiraPriceKW,
      isPampFPriceKW,
      isPampRPriceKW,
      isPampSPricKW,
      isGoldCalc,
      isgoldNews
    } = this.state;
    return (
      <div className="App">
        <h2> مجموعة العطار و يونس للمجوهرات </h2>

        <div className="row">
          <button
            className="btn btn-dark btn-sm"
            onClick={this.handleChangeToGoldPriceKw}
          >
            <Container className="price-btn">
              <Image src="assets/btns/goldprice-btn.jpg" className="btn-icon" />
            </Container>
          </button>
          <button
            className="btn btn-dark btn-sm"
            onClick={this.handleChangeToAloraPriceKW}
          >
            <Container className="price-btn">
              <Image src="assets/btns/alora-btn.jpg" className="btn-icon" />
            </Container>
          </button>
        </div>
        <div className="row">
          <button
            className="btn btn-dark btn-sm"
            onClick={this.handleChangeToLiraPriceKW}
          >
            <Container className="price-btn">
              <Image src="assets/btns/lira-btn.jpg" className="btn-icon" />
            </Container>
          </button>
          <button
            className="btn btn-dark btn-sm"
            onClick={this.handleChangeToPampFKW}
          >
            <Container className="price-btn">
              <Image src="assets/btns/swiss-btn.jpg" className="btn-icon" />
            </Container>
          </button>
        </div>
        <div className="row">
          <button
            className="btn btn-dark btn-sm"
            onClick={this.handleChangeToGoldCalc}
          >
            <Container className="price-btn">
              <Image src="assets/btns/btn-calc.jpg" className="btn-icon" />
            </Container>
          </button>
          <button
            className="btn btn-dark btn-sm"
            onClick={this.handleChangeToGoldZakat}
          >
            <Container className="price-btn">
              <Image src="assets/btns/zakat-btn.jpg" className="btn-icon" />
            </Container>
          </button>
        </div>
        {isGoldPriceKW === true ? (
          <div>
            <GoldTicker />
            <GoldPriceKW />
          </div>
        ) : null}
        {isAloraPriceKW === true ? <AloraPriceKW /> : null}
        {isLiraPriceKW === true ? <LiraPriceKW /> : null}
        {isPampFPriceKW === true ? <PampFortunaPrice /> : null}
        {isPampRPriceKW === true ? <PampRosaPrice /> : null}
        {isPampSPricKW === true ? <PampSpecialEditionPrice /> : null}
        {isGoldCalc === true ? <GoldCalc875 /> : null}
        {isgoldNews === true ? <GoldZakatKW /> : null}
      </div>
    );
  }
}

export default App;
