import React, { Component } from "react";
import ReactLoading from "react-loading";
import { Container, Image } from "react-bootstrap";

import {
  API,
  ENDPOINT,
  ACCESS_KEY,
  BASE_CURRENCY,
  SYMBOLS
} from "../../helpers/GoldApiKW";

class LiraPriceKW extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rates: [],
      timestamp: "",
      isLoading: false
    };
  }

  componentDidMount() {
    this.intervalId = setInterval(() => this.getData(), 60000);
    this.getData();
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  getData = () => {
    this.setState({ isLoading: true });
    fetch(
      API + ENDPOINT + "?access_key=" + ACCESS_KEY + BASE_CURRENCY + SYMBOLS
    )
      .then((response) => response.json())
      .then((data) =>
        this.setState({
          rates: data.rates,
          timestamp: data.timestamp,
          isLoading: false
        })
      );
  };

  render() {
    const { rates, isLoading } = this.state;

    if (isLoading) {
      return (
        <div className="sj_logo">
          <h1>Loading Gold Prices...</h1>

          <ReactLoading
            className="sj_logo"
            type="bars"
            color="#c4ae78"
            height={200}
            width={200}
          />
        </div>
      );
    }

    return (
      <div>
        <div className="date">
          {new Date().toLocaleString("en-US", { day: "2-digit" })}/
          {new Date().toLocaleString("en-US", { month: "2-digit" })}/
          {new Date().getFullYear()}
          <p>
            {new Date().toLocaleString("en-US", {
              hour: "numeric",
              minute: "numeric",
              second: "numeric",
              hour12: true
            })}
          </p>
        </div>
        <table className="price-table">
          <tbody>
            <tr>
              <th colSpan="2">
                <Container className="price-container">
                  <Image src="assets/imgs/liras.jpg" className="bar-brand" />
                  <h3 className="price-title">الليرات العثمانية عيار 22</h3>
                </Container>
              </th>
            </tr>
            <tr>
              <td dir="rtl">
                {Number((rates.XAU / 31.1034807) * 0.916667 * 36 + 18).toFixed(
                  3
                )}{" "}
                د.ك
              </td>
              <td>مخمس عثماني عيار 22</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number(
                  (rates.XAU / 31.1034807) * 0.916667 * 18 + 9.25
                ).toFixed(3)}{" "}
                د.ك
              </td>
              <td>نص مخمس عثمانية عيار 22</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number(
                  (rates.XAU / 31.1034807) * 0.916667 * 7.2 + 2.75
                ).toFixed(3)}{" "}
                د.ك{" "}
              </td>
              <td>ليرة عثمانية عيار 22</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number(
                  (rates.XAU / 31.1034807) * 0.916667 * 3.6 + 2.6
                ).toFixed(3)}{" "}
                د.ك
              </td>
              <td>نص ليرة عثمانية عيار 22</td>
            </tr>
            <tr>
              <td dir="rtl">
                {Number(
                  (rates.XAU / 31.1034807) * 0.916667 * 1.8 + 2.25
                ).toFixed(3)}{" "}
                د.ك
              </td>
              <td>ربع ليرة عثمانية عيار 22</td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export default LiraPriceKW;
